<template>
    <div class="art" ref="wrapperEl">
        <div class="actions" ref="actionsEl">
            <button class="nag-btn negative clickable" style="width:250px; margin:5px" @click="goHome">Start Over</button>
            <button ref="saveBtn" class="nag-btn clickable" style="width:250px; margin:5px;" @click="takeScreenshot">Save</button>
        </div>
        <ArtActual
            ref="artContent"
            :selectedPaintType="this.paintType"
            :totalAssets="this.totalAssets"
            :selectedBackground="this.background"
            :selectedColors="this.colors"
            :removeSaveButton="this.removeSaveButton"
            :removeWatermark="this.removeSideWatermark"
        />
        <div ref="instructionsEl" class="clickable start-btn-container" @click="start">
            <img ref="instructionsPanelEl" :src="this.startBtnSrc" class="start-btn" />
        </div>
    </div>
</template>

<script>
    // Sample import for your component
    import Vue from 'vue'
    import ArtActual from './ArtActual'
    import { TweenMax, Power4 } from 'gsap'

    export default {
        name: "Art",
        components: {
            ArtActual
        },
        props: ['selectedColorScheme', 'selectedBrushes', 'selectedCanvas', 'removeGlobalElements'],
        data () {
            return {
                colors: [],
                background: 'art/bg/1.jpg',
                paintType: "Dry",
                totalAssets: 0,
                started: false,
                startBtnSrc: 'art/splatter-desktop.png',
                startBtnShow: false,
            }
        },
        mounted () {
            TweenMax.set(this.$refs.wrapperEl, { autoAlpha: 0, overwrite: true });
            TweenMax.set(this.$refs.instructionsPanelEl, {
                y: 0, autoAlpha: 0, overwrite: true,
            });
            TweenMax.set(this.$refs.actionsEl, {
                autoAlpha: 0, overwrite: true,
            });
        },
        methods: {
            removeSaveButton () {
                this.$refs.saveBtn.remove();
                this.removeGlobalElements()
            },
            removeSideWatermark () {
                this.removeGlobalElements()
            },
            init () {
                let {selectedColorScheme, selectedBrushes, selectedCanvas} = this;
                console.log(selectedColorScheme, selectedBrushes, selectedCanvas)
                // Paint
                const totalAssetsByPaintType = {
                    'Dry': 20,
                    'Wet': 20,
                    'Brush': 19,
                };

                if (!selectedBrushes) {
                    selectedBrushes = 3;
                } else {
                    selectedBrushes = Number(selectedBrushes);
                }
                if (selectedBrushes === 3) {
                    this.paintType = "Brush";
                    // this.startBtnSrc = (!this.$isMobile()) ? "art/stroke-desktop.png" : "art/stroke-mobile.png" ;
                    this.startBtnSrc = "art/stroke-mobile.png";
                    this.totalAssets = totalAssetsByPaintType['Brush'];
                } else if (selectedBrushes === 1) {
                    this.paintType = "Dry";
                    // this.startBtnSrc = (!this.$isMobile()) ? "art/splatter-mobile.png" : "art/splatter-mobile.png" ;
                    this.startBtnSrc = "art/splatter-mobile.png";
                    this.totalAssets = totalAssetsByPaintType['Dry'];
                } else if (selectedBrushes === 2) {
                    this.paintType = "Wet";
                    // this.startBtnSrc = (!this.$isMobile()) ? "art/splatter-mobile.png" : "art/splatter-mobile.png" ;
                    this.startBtnSrc = "art/splatter-mobile.png";
                    this.totalAssets = totalAssetsByPaintType['Wet'];
                }

                // Background
                if (!selectedCanvas || Number(selectedCanvas) < 1 || Number(selectedCanvas) > 4) {
                    selectedCanvas = 1;
                } else {
                    selectedCanvas = Number(selectedCanvas);
                }
                this.background = `art/bg/Portrait-${selectedCanvas}.jpg`;

                // Color Scheme
                if (!selectedColorScheme || Number(selectedColorScheme) < 1 || Number(selectedColorScheme) > 5) {
                    selectedColorScheme = 1;
                } else {
                    selectedColorScheme = Number(selectedColorScheme);
                }
                if (selectedColorScheme === 1) {
                    this.colors = [0xabdfe3, 0x60bfa4, 0xe5a569, 0x000000, 0x000000];
                    // this.colors = [0xFF0000];
                } else if (selectedColorScheme === 4) {
                    this.colors = [0x214970, 0x8ea9c0, 0x000000, 0xffffff, 0x74e4d5]
                    // this.colors = [0x00FF00];
                } else if (selectedColorScheme === 2) {

                    // this.colors = [0x0000FF];
                    this.colors = [0x1c5feb, 0xfadd69, 0x030b4f, 0x0b0d76, 0x52b34f];
                } else if (selectedColorScheme === 3) {
                    // this.colors = [0xFF00FF];
                    this.colors = [0x65436f, 0x56a59d, 0xd0c7b6, 0x184897, 0x000000, 0x000000];
                } else if (selectedColorScheme === 5) {
                    // this.colors = [0xFF00FF];
                    this.colors = [0xF82633, 0x6F97D0, 0xB887C1, 0xFF4E24, 0x29B42F, 0x8EA9C0, 0x83CD18, 0xFF9900, 0xFFE300, 0x00A984, 0xFF3C77, 0x008ED2, 0xFFD300];
                }

                // this.$refs.startBtnEl.src = this.startBtnSrc;
                Vue.nextTick(() => {
                    TweenMax.set(this.$refs.wrapperEl, { autoAlpha: 1, overwrite: true });
                    this.$refs.artContent.init();
                    setTimeout(() => {
                        this.startBtnShow = true;
                    }, 100)
                });
            },
            start () {
                this.startBtnShow = false;
                this.$refs.artContent.start();
                this.started = true;
                this.$refs.instructionsEl.classList.remove("clickable");
                TweenMax.to(this.$refs.instructionsPanelEl, 0.4, {
                    y: 0, autoAlpha: 0, overwrite: true,
                    ease: Power4.easeOut,
                    onComplete: () => {
                        this.$refs.instructionsEl.style.display = "none";
                    }
                });
                TweenMax.to(this.$refs.actionsEl, 0.4, {
                    y: 0, autoAlpha: 1, overwrite: true,
                })
            },
            showInstructions ()  {
                TweenMax.to(this.$refs.instructionsPanelEl, 0.8, {
                    y: -20, autoAlpha: 1, overwrite: true,
                    ease: Power4.easeOut
                });
            },
            takeScreenshot () {
                this.$refs.artContent.screenshot();
            },
            goHome () {
                window.location.reload();
            }
        }
    }
</script>

