<script>
    import {TweenMax} from 'gsap';
    export default {
        name: "StepContentBase",
        props: ['left'],
        data () {
            return {
                scale: 1.0
            }
        },
        mounted() {
            window.addEventListener('resize', () => this.onResizeHandler());
            this.onResizeHandler();
        },
        methods: {
            onResizeHandler () {
                const contentWidth = 1000;
                if (window.innerWidth < contentWidth) {
                    const scale = window.innerWidth / contentWidth;
                    this.$refs.wrapper.style.transform = `scale(${ scale })`
                } else {
                    this.$refs.wrapper.style.transform = `scale(1)`
                }
                this.$refs.wrapper.style.height = `${ window.innerHeight }px`;
            },
            raiseToTop() {
                // this.$refs.wrapper.style.zIndex = 1000;
            },
            lowerToBottom() {
                // this.$refs.wrapper.style.zIndex = 400;
            },

            animateInFromRight () {
                console.log("Step1Content.animateInFromRight");
                for (let key in this.$refs) {
                    console.log("-" + key);
                    if ( key === "wrapper" ) continue;
                    this.$refs[key].animateInFromRight();
                }
                this.raiseToTop();
            },
            animateInFromLeft () {
                this.lowerToBottom();
                console.log("Step1Content.animateInFromLeft");
                for (let key in this.$refs) {
                    console.log("-" + key);
                    if ( key === "wrapper" ) continue;
                    this.$refs[key].animateInFromLeft();
                }
                TweenMax.delayedCall(0.5, () => {
                    this.raiseToTop();
                })
            },

            animateOutToLeft () {
                this.lowerToBottom();
                console.log("Step1Content.animateOut");
                for (let key in this.$refs) {
                    console.log("-" + key);
                    if ( key === "wrapper" ) continue;
                    this.$refs[key].animateOutToLeft();
                }
            },

            animateOutToRight () {
                this.lowerToBottom();
                console.log("Step1Content.animateOut");
                for (let key in this.$refs) {
                    console.log("-" + key);
                    if ( key === "wrapper" ) continue;
                    this.$refs[key].animateOutToRight();
                }
            }
        }
    }
</script>