<template>
    <div class="blur-background-container">
        <div class="blur-background-mask" ref="mask">
            <div class="blur-background" ref="background"/>
        </div>
    </div>
</template>

<script>
    import { TweenMax, Expo } from 'gsap';

    export default {
        name: "BlurBackground",
        mounted () {
            TweenMax.set(this.$refs.background, { x: '100%' });
            this.$refs.background.style.backgroundImage = `url('img/blur-bg@2x.jpg')`;
        },
        methods: {
            animateIn() {
                console.log("BlurBackground.animateIn");
                TweenMax.set(this.$refs.background, { x: '100%' });
                TweenMax.to(this.$refs.background, 0.6, {
                    x: 0,
                    overwrite: true,
                    ease: Expo.easeOut,
                });
            },
            animateOut() {
                TweenMax.set(this.$refs.background, { x: 0 });
                TweenMax.to(this.$refs.background, 0.6, {
                    x: '100%',
                    overwrite: true,
                    ease: Expo.easeOut,
                });
            }
        }
    }
</script>

<style scoped>
    .blur-background-container {
        position: absolute;
        top: 0;
        right: 0;
        width: 100vw;
        height: 100%;
        z-index: 100;
    }

    .blur-background-mask {
        width: 100vw;
        height: 100%;
        overflow: hidden;
        /*border: 1px solid red;*/
    }

    .blur-background {
        width: 100vw;
        height: 100vh;
        background-size: cover;
    }
</style>