<template>
    <div class="step-content-container" ref="wrapper">
        <div class="step-content">
            <ImageBall v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection1" x="-200" y="-200"
                               src="img/step1-splatter@2x.png"
                               selectedSrc="img/step1-splatter-selected@2x.png"/>
            <ImageBall v-on:ball-selected="onBallSelected"  onPressed="onBallSelected" ref="selection2" x="120" y="-280"
                               src="img/step1-drops@2x.png"
                               selectedSrc="img/step1-drops-selected@2x.png" />
            <ImageBall v-on:ball-selected="onBallSelected" onPressed="onBallSelected" ref="selection3" x="-100" y="-500"
                               src="img/step1-strokes@2x.png"
                               selectedSrc="img/step1-strokes-selected@2x.png" />
        </div>
    </div>
</template>

<script>
    import ImageBall from "./ImageBall";
    // import Ball from "./Ball";
    import StepContentBase from "./StepContentBase";
    export default {
        name: "Step1Content",
        components: {
            // Ball,
            ImageBall,
        },
        extends: StepContentBase,
        methods: {
            onBallSelected (obj) {
                if (this.$refs.selection1 !== obj) this.$refs.selection1.deselect();
                if (this.$refs.selection2 !== obj) this.$refs.selection2.deselect();
                if (this.$refs.selection3 !== obj) this.$refs.selection3.deselect();

                if (this.$refs.selection1 === obj) {
                    this.$emit('selected', 1);
                } else if (this.$refs.selection2 === obj) {
                    this.$emit('selected', 2);
                } else if (this.$refs.selection3 === obj) {
                    this.$emit('selected', 3);
                }
            },
        }
    }
</script>