import {Quad} from "gsap";
<template>
    <div class="ball-container" ref="centerEl">
        <!--        <h1 class="debug" style="font-size: 4em; position: absolute; top: 300px; left: 300px; z-index: 300;">{{ id }}</h1>-->
        <img @click="onPressHandler" class="ball clickable" ref="ballEl" :src="this.currentSrc"  @mouseover="onMouseOverHandler" @mouseout="onMouseOutHandler"/>
    </div>
</template>

<script>
    import Ball from "./Ball";
    import {TweenMax, Back, Quad} from 'gsap';
    export default {
        name: "ImageBall",
        extends: Ball,

        props: [ 'src', 'selectedSrc', 'onPressed', 'id'],
        data () {
            return {
                currentSrc: '', stop: true, selected: false,
            }
        },
        mounted () {
            this.onPressHandler = this.onPressHandler.bind(this);
            this.currentSrc = this.src;
        },

        methods: {
            onPressHandler () {
                this.selected = true;
                this.currentSrc = this.selectedSrc;
                this.$emit("ball-selected", this);
                // this.onPressed(this);
                TweenMax.to(this.$refs.ballEl, 0.4, { ease: Back.easeOut, force3D: true, scale: 0.9, z: -0.01, overwrite: true });
            },
            deselect () {
                this.selected = false;
                this.currentSrc = this.src;
                TweenMax.to(this.$refs.ballEl, 0.4, { ease: Back.easeOut, force3D: true, scale: 0.8, z: -0.01, overwrite: true });
            },
            onMouseOverHandler () {
                if ( this.selected ) return;
                console.log("AnimatedButton.onMouseOverHandler");
                TweenMax.to(this.$refs.ballEl, 0.2, { scale: 0.9, force3D: true, ease: Quad.easeOut, overwrite: true });
                // TweenMax.to(this.$refs.ballEl, 0.2, { scale: 0.45, ease: Quad.easeOut, overwrite: true });
            },
            onMouseOutHandler () {
                if ( this.selected ) return;
                console.log("AnimatedButton.onMouseOutHandler");
                TweenMax.to(this.$refs.ballEl, 0.2, { scale: 0.8, force3D: true, ease: Quad.easeOut, overwrite: true });
                // TweenMax.to(this.$refs.ballEl, 0.2, { scale: 0.4, ease: Quad.easeOut, overwrite: true });
            },
        }
    }
</script>

<style scoped>
    .ball-container {
        position: absolute;
        width: 10px;
        height: 10px;
        z-index: 1000;
    }
    .ball {
        /*transform: scale(0.4);*/
        transform: scale(0.8) translateX(50%) translateY(50%);
        /*width: 422px;*/
        /*height: 422px;*/
    }
</style>