<template>
    <div class="ball-container" ref="centerEl">
        <div class="ball" ref="ballEl" />
    </div>
</template>

<script>
    import { TweenMax, Quad, Power4 } from 'gsap';

    export default {
        name: "Ball",
        props: ['radius', 'color', 'x', 'y'],
        data () {
            return {
                rightX: 0,
                fx: 0,
                fy: 0,
                leftX: 0,
                swimming: false,
            }
        },
        mounted () {
            this.fx = Number(this.x) + (screen.width * 0.5);
            this.fy = Number(this.y) + (screen.height * 0.48);
            this.rightX = screen.width * 4;
            this.leftX = -screen.width * 4;

            TweenMax.set(this.$refs.centerEl, { x: this.rightX, y: this.fy });

            this.$refs.ballEl.style.borderRadius = `${ Number(this.radius) }px`;
            this.$refs.ballEl.style.width = `${ Number(this.radius) * 2 }px`;
            this.$refs.ballEl.style.height = `${ Number(this.radius) * 2 }px`;
            this.$refs.ballEl.style.marginLeft = `${ -Number(this.radius) }px`;
            this.$refs.ballEl.style.marginTop = `${ -Number(this.radius) }px`;
            this.$refs.ballEl.style.backgroundColor = this.color;
        },

        methods: {
            startSwim () {
                return;
                // if ( ! this.swimming ) {
                //     this.swimming = true;
                //     this.swim();
                // }
            },
            stopSwim () {
                this.swimming = false;
            },
            swim () {
                // console.log("Animate");
                const fx = Math.random() * 30 - 15;
                const fy = Math.random() * 30 - 15;
                const time = Math.sqrt(fx * fx + fy * fy) / 4;
                const delay = Math.random() * 0.3;
                const vars = {
                    delay,
                    x: fx,
                    y: fy,
                    ease: Quad.easeInOut,
                    overwrite: true,
                    onComplete: () => {
                        if ( this.swimmimg ) {
                            this.swim()
                        }
                    }
                };
                TweenMax.to(this.$refs.ballEl, time, vars);
            },
            animateInFromRight () {
                TweenMax.fromTo(this.$refs.centerEl,
                    (Math.random() * 0.15) + .4,
                    { x: this.rightX, y: this.fy },
                    { force3D: true, overwrite: true, x: this.fx, ease: Power4.easeOut, onComplete: () => this.startSwim() }
                    );
            },
            animateInFromLeft () {
                TweenMax.fromTo(
                    this.$refs.centerEl,
                    (Math.random() * 0.15) + .4,
                    { x: this.leftX, y: this.fy },
                    { force3D: true, overwrite: true, x: this.fx, ease: Power4.easeOut, onComplete: () => this.startSwim() }
                    );
            },
            animateOutToRight () {
                TweenMax.to(this.$refs.centerEl, (Math.random() * 0.25) + .4, { force3D: true, overwrite: true, x: this.rightX, ease: Quad.easeOut, onComplete: () => this.stopSwim() });
            },
            animateOutToLeft () {
                TweenMax.to(this.$refs.centerEl, (Math.random() * 0.25) + .4, { force3D: true, overwrite: true, x: this.leftX, y: this.fy, ease: Quad.easeOut, onComplete: () => this.stopSwim() });
            },
        }
    }
</script>

<style scoped>
    .ball-container {
        position: fixed;
        z-index: 1000;
        width: 1px;
        height: 1px;
    }
</style>