<script>
    import { TweenMax, Quad } from 'gsap';
    export default {
        name: "AnimatedButton",
        methods: {
            onMouseOverHandler () {
                console.log("AnimatedButton.onMouseOverHandler");
                TweenMax.to(this.$refs.btnEl, 0.1, { scale: 1.1, ease: Quad.easeOut, overwrite: true });
            },
            onMouseOutHandler () {
                console.log("AnimatedButton.onMouseOutHandler");
                TweenMax.to(this.$refs.btnEl, 0.1, { scale: 1, ease: Quad.easeOut, overwrite: true });
            },
        }
    }
</script>

