<template>
    <div ref="transitionEl" class="transition">
        <LoaderAnimation/>
    </div>
</template>

<script>
    import { TweenMax, Power4 } from 'gsap';
    import LoaderAnimation from "../UI/LoaderAnimation";
    export default {
        name: "Transition",
        components: {
            LoaderAnimation
        },
        mounted () {
            TweenMax.set(this.$refs.transitionEl, { width: '100vw', height: 1, x: -5000 })
        },
        methods: {
            transitIn () {
                TweenMax.set(this.$refs.transitionEl, { width: '100vw', height: 1, x: 0 });
                TweenMax.to(this.$refs.transitionEl, 0.8, {
                    height: '100vh',
                    overwrite: true,
                    ease: Power4.easeOut,
                    onComplete: () => {
                        this.$emit('transit-in-complete')
                    }
                });
            },
            transitOut () {
                // TweenMax.set(this.$refs.transitionEl, { top: 0, height: 1, x: 0 });
                TweenMax.to(this.$refs.transitionEl, 0.4, {
                    height: 0,
                    overwrite: true,
                    ease: Power4.easeOut,
                    onComplete: () => {
                        this.$emit('transit-out-complete')
                    }
                });
            }
        }
    }
</script>

<style scoped>
.transition {
    width: 100vw;
    height: 100vh;
    background-color: #EFEFEF;
    bottom: 0;
    left: 0;
}
</style>