<template>
    <div class="btn-container clickable">
        <a class="btn" @click="onPress" ref="btnEl" @mouseover="onMouseOverHandler" @mouseout="onMouseOutHandler">
            <p class="label white">{{ label }}</p>
            <img :src="src" :alt="label"/>
        </a>
    </div>
</template>

<script>
    import AnimatedButton from "./AnimatedButton";

    export default {
        name: "Button",
        props: ['color', 'label', 'onPressed'],
        extends: AnimatedButton,
        data () {
            return { src:'' }
        },
        mounted () {
            if ( this.color === 'gray' ||  this.color === 'grey' ) {
                this.src = "img/step-back-btn@2x.png"
            } else if ( this.color === 'red' ) {
                console.log("red");
                this.src = "img/step-next-btn@2x.png"
            }
        },
        methods: {
            onPress () {
                console.log("Button Pressed", this.onPressed)
                if ( this.onPressed ) {
                    this.onPressed();
                }
            }
        }
    }
</script>

