<template>
    <div class="left-side-container" ref="wrapper">
        <div class="step-container" ref="animateEl">
            <div class="headline-resize-wrapper" ref="headlineEl">
                <img :src="instructionsSrc" alt="" class="image">
            </div>
            <div class="button-group" ref="buttonGroupEl">
                <Button color="grey" label="Back" :onPressed="onBack" />
                <Button color="red" label="Next" :onPressed="onNext" :class="{ 'disabled': !canNext, 'clickable': canNext }" />
            </div>
        </div>
    </div>
</template>

<script>
    import { TweenMax, Linear } from 'gsap';
    import Button from "../UI/Button";

    export default {
        name: "Step",
        components: {
            Button
        },
        props: ['step', 'nextPressedHandler', 'backPressedHandler', 'canNext'],
        data () {
            return { show: false, instructionsSrc: '' }
        },
        mounted () {
            window.addEventListener('resize', () => this.onResizeHandler());
            this.onResizeHandler();

            this.instructionsSrc = `img/step${ this.step }-headline@2x.png`;
            this.getOut();
        },
        methods: {
            onResizeHandler () {
                const contentWidth = 550;
                if (window.innerWidth < contentWidth) {
                    const scale = window.innerWidth / contentWidth;
                    this.$refs.headlineEl.style.transform = `scale(${ scale / 2 })`;
                    this.$refs.buttonGroupEl.style.transform = `scale(${ scale })`;
                } else {
                    this.$refs.headlineEl.style.transform = `scale(0.5)`;
                    this.$refs.buttonGroupEl.style.transform = `scale(1)`;
                }


                this.$refs.wrapper.style.height = `${ window.innerHeight }px`;
                // this.$refs.wrapper.style.transform = `scale(${ screen.width / window.innerWidth })`;
            },
            getOut () {
                TweenMax.set(this.$refs.wrapper, { left: "-5000px", alpha: 0 });
            },
            getIn () {
                TweenMax.set(this.$refs.wrapper, { left: "0px" });
            },
            animateIn () {
                this.getIn();
                TweenMax.to(this.$refs.wrapper, 0.3, { alpha: 1.0, ease: Linear.easeNone });
            },
            animateOut () {
                // TODO: Kill mouse action
                TweenMax.to(this.$refs.wrapper, 0.3, { alpha: 0, ease: Linear.easeNone });
                TweenMax.delayedCall(0.3, () => this.getOut());
            },
            onBack () {
                if ( this.backPressedHandler ) {
                    this.backPressedHandler();
                }
            },
            onNext () {
                if ( this.nextPressedHandler ) {
                    this.nextPressedHandler();
                }
            }
        }
    }
</script>

