<template>
    <div ref="wrapperEl" class="sy-text-watermark">
        <span ref="textEl">Background image created by Yeo Shih Yun</span>
    </div>
</template>

<script>
    import { TweenMax, Expo } from 'gsap'
    export default {
        name: "TextWatermark",
        mounted() {
            this.positionForHome()
            // this.hide();
        },
        methods: {
            hide () {
                TweenMax.to(this.$refs.wrapperEl, 0.7, { autoAlpha: 0,  ease: Expo.easeOut, overwrite: true });
            },
            show () {
                TweenMax.to(this.$refs.wrapperEl, 0.7, { autoAlpha: 1, ease: Expo.easeOut, overwrite: true });
            },
            positionForHome () {
                TweenMax.set(this.$refs.textEl, { x: '100%', rotation: 90, overwrite: true })
                // this.$refs.wrapperEl.style.bottom = "10px";
            },
            positionForArt () {
                // TweenMax.se
                this.$refs.textEl.style.bottom = 0;
                this.$refs.textEl.style.transformOrigin = "100% 0";
                TweenMax.set(this.$refs.textEl, { x: 0, rotation: 90, overwrite: true })
            }
        }
    }
</script>

<style scoped>
    .sy-text-watermark {
        z-index: 1500;
        position: fixed;
        top: 0;
        left: 0;

        padding: 8px;
        color: #FFF;
        font-size: 0.9em;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
        pointer-events: none;
    }
    .sy-text-watermark span {
        transform-origin: 0 0;
        position: fixed;
    }
</style>